p{
  margin: 0px;
  color: orange;
}
#root {
  min-height: 100vh;
  height: 100%;
  background-color: #191919;
  color: white;
  overflow-x: hidden;
}
#appContainer{
  width: 100vw;
  margin: 0px auto;
  font-size: 22px;
  padding: 20px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#appHeader{
  font-size: 30px;
  margin: 3px;
}
#appSubHeader{
  font-size: 25px;
  margin: 5px;
}
#filterControl{
  display: flex;
  width: 100%;
  margin: 0px;
  align-items: center;
  justify-content: center;
  background-color: #585858;
}
#contentContainer{
  background-color: null;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}
.itemContainer{
  display: flex;
  align-content: center;
  justify-content: center;
  min-height: 200px;
  width: 350px;
  margin: 30px;
}
.itemContainer > .itemContainerFront{
  position: absolute;
  transform: perspective(600px) rotateY(0deg);
  backface-visibility: hidden;
  transition: transform .25s linear 0s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: navy;
  border-radius: 25px;
  box-shadow: 0px 0px 10px 2px rgba(255,255,255,0.75);
  -webkit-box-shadow: 0px 0px 10px 2px rgba(255,255,255,0.75);
  -moz-box-shadow: 0px 0px 10px 2px rgba(255,255,255,0.75);
}
.itemContainer > .itemContainerBack{
  position: absolute;
  transform: perspective(600px) rotateY(180deg);
  backface-visibility: hidden;
  transition: transform .25s linear 0s;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: navy;
  border-radius: 25px;
  box-shadow: 0px 0px 10px 2px rgba(255,255,255,0.75);
  -webkit-box-shadow: 0px 0px 10px 2px rgba(255,255,255,0.75);
  -moz-box-shadow: 0px 0px 10px 2px rgba(255,255,255,0.75);
}
.itemContainer:hover > .itemContainerFront{
  transform: perspective(600px) rotateY(-180deg);
}
.itemContainer:hover > .itemContainerBack{
  transform: perspective(600px) rotateY(0deg);
}

.itemName{
  width: 325px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 5px;
  text-decoration: none;
  color: orange;
}
.itemImage{
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 163px;
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
}
.itemImage textarea{
  background-color: rgba(100, 100, 100, .85 );
  text-decoration: none;
  color: #FFF;
  font-weight: bold;
  text-shadow: 1px 1px #000;
  text-align: center;
  width: 90%;
  border-radius: 10px;
  resize: none;
}
.itemImage textarea:hover{
  cursor: pointer;
}
.itemInfo{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 25px;
}
label {
    margin: auto 10px;
}
input[type="text"] {
  height: 20px;
  margin: 5px 0px;
  font-size: 19px;
}
input[type="checkbox"] {
  height: 17px;
  width: 17px;
}
hr {
  margin: 0px;
  width: 100%;
}
@media only screen and (max-width: 825px) {
  #appContainer{
    width: 100vw;
  }
}
@media only screen and (max-width: 635px) {
  #appContainer{
    width: 100vw;
    font-size: 16px;
  }
  #appHeader{
    font-size: 20px;
  }
  #appSubHeader{
    font-size: 16px;
  }
  #filterControl{
    flex-direction: column;
  }
  .itemContainer{
    margin: 30px auto;
  }
}
  @media only screen and (max-width: 400px) {
    #appContainer{
      font-size: 14px;
    }
    #appHeader{
      font-size: 16px;
    }
    #appSubHeader{
      font-size: 14px;
    }

    .itemContainer, .itemContainerFront, .itemContainerBack{
      width: 314px;
    }
  }
